import {observer} from "mobx-react-lite";
import {subscribeListState} from "../store";
import {Button} from "react-bootstrap";
import {useState} from "react";
import {BuySubscribe} from "./BuySubscribe";

export const SubscribeList = observer(() => {
    const [selectSubscribeItemId, setSelectSubscribeItemId] = useState<number>(0);

    return <>
        {
            selectSubscribeItemId === 0 &&
                subscribeListState.list?.map(sub => {
                    return <div className="d-flex justify-content-between py-2 text-center">
                        <div className="w-30">
                            {sub.name}
                        </div>
                        <div className="w-30">
                            <label htmlFor="subscribe-item">{sub.price}руб.</label>
                        </div>
                        <div className="w-30">
                            <Button variant="dark" onClick={() => setSelectSubscribeItemId(sub.id)}>
                                К оплате
                            </Button>
                        </div>
                    </div>
                })
        }

        {
            selectSubscribeItemId > 0 &&
                <BuySubscribe id={selectSubscribeItemId}/>
        }
    </>
})
