import {Button} from "react-bootstrap";
import {TicketList} from "./component/TicketList";
import {Index as AddEditForm} from "./component/add-ticket-modal/Index";
import {openModal} from "../../../../common/component/modal/modalStore";
import {observer} from "mobx-react-lite";

export const Index = observer(() => {

    return <>
        <div>
            <h4>Помощь</h4>
        </div>
        <hr className="mt-0"/>
        <div>
            <Button variant="dark" onClick={() => openModal(<AddEditForm/>)}>
                Создать обращение
            </Button>
        </div>
        <TicketList/>
    </>
})
