import {Nav} from "react-bootstrap";
import {Link} from "react-router-dom";
import {
    DoorOpenFill,
    FileBinaryFill, GearFill,
    HddRackFill,
    HouseDoorFill,
    InfoCircleFill,
    ListTask,
    PeopleFill, PersonPlusFill, Windows
} from "react-bootstrap-icons";
import {observer} from "mobx-react-lite";
import {authState, removeToken} from "../../store/auth";
import {userRole} from "../../store/model/IUser";

export const LeftMenu = observer(() => {

    return <>
        {/*Обычное меню - Начало*/}
        <div className="nav d-flex flex-column py-2 left-menu">
            <div className="d-none d-lg-block text-center">
                <div className="mx-auto mt-2">
                    <h4>LockVPN</h4>
                </div>
                <hr className="my-0 mt-2"/>
            </div>

            <Nav className="flex-column">
                <Link className="nav-link d-flex justify-content-between text-center align-items-center px-lg-5 py-3" to="/">
                    <HouseDoorFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">Главная</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between text-center align-items-center px-lg-5 py-3" to="/referral">
                    <PersonPlusFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">Реф. программа</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between text-center align-items-center px-lg-5 py-3" to="/help">
                    <GearFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-center text-white d-none d-lg-block">Настройки</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between align-items-center px-lg-5 py-3" to="/tickets">
                    <InfoCircleFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">Поддержка</span>
                </Link>
                <div className="nav-link d-flex justify-content-between align-items-center px-lg-5 py-3" style={{cursor: "pointer"}} onClick={removeToken}>
                    <DoorOpenFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white d-none d-lg-block">Выход</span>
                </div>
                <hr className="py-2 my-0"/>
                <div className="d-block d-lg-flex justify-content-around mx-lg-4" title="App Gallery">
                    <div className="mx-1 text-center my-auto p-2">
                        <a href="https://appgallery.huawei.com/app/C110194213" className="text-decoration-none"
                           target="_blank" rel="noreferrer">
                            <img src={`${window.location.origin}/img/appgallery-icon.svg`} alt="App Gallery" width={25} height={25}
                                 color="white"/>
                        </a>
                    </div>
                    <div className="mx-1 text-center my-auto p-2" title="4PDA">
                        <a href="https://4pda.to/forum/index.php?showtopic=1083324" className="text-decoration-none"
                           target="_blank" rel="noreferrer">
                            <img src={`${window.location.origin}/img/4pda_icon.svg`} alt="4 PDA" width={25} height={25} color="white"/>
                        </a>
                    </div>
                    <div className="mx-1 text-center my-auto p-2" title="Apk Pure">
                        <a href="https://apkpure.net/p/com.lockvpnandroidapp" className="text-decoration-none bg-white px-1 pb-1 border rounded-2"
                           target="_blank" rel="noreferrer">
                            <img src={`${window.location.origin}/img/apkpure-icon.svg`} alt="Apk Pure" width={23} height={23} color="white"/>
                        </a>
                    </div>
                    <div className="mx-1 text-center my-auto p-2">
                        <a className="not-hover text-black" href="https://lockvpn.me/files/windowsapp/LockVPNSetup.msi">
                            <Windows color="white" width={25} height={25}/>
                        </a>
                    </div>
                </div>

            </Nav>

            {authState.user?.role === userRole.admin &&
                <>
                    <hr/>
                    <Nav className="flex-column mx-lg-4">
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/users">
                            <PeopleFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white d-none d-lg-block">Пользователи</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center"
                              to="/admin/servers">
                            <HddRackFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Сервера</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/logs">
                            <ListTask className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Логи</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/file">
                            <FileBinaryFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Файлы</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/tickets">
                            <InfoCircleFill className="me-2" color="white" width={25} height={25}/>
                            <span className="w-100 text-center text-white white d-none d-lg-block">Обращения</span>
                        </Link>
                    </Nav>
                </>

            }
        </div>
        {/*Обычное меню - Конец*/}
    </>
})
