import {action, observable} from "mobx";
import {getAsync} from "../../../../common/fetch/getAsync";
import {IAccessPosition} from "../model/IAccessPosition";
import {postAsync} from "../../../../common/fetch/postAsync";
import {closeLastModal} from "../../../../common/component/modal/modalStore";
import {successToast} from "../../../../common/toast/success";

const subscribeListState = observable({list: null as unknown as IAccessPosition[]});

const selectSubscribeItem = observable({
    referralBalance: 0,
    subscribeItem: null as unknown as IAccessPosition
});

const getSubscribesAsync = action(async() => {
    await getAsync("/Payment/AccessPositions").then(response => {
        subscribeListState.list = response.data.accessPositions;
    })
})

const getSubscribeItemAsync = action(async (id: number, sale: number) => {
    await getAsync(`/Payment/GetSubscribeItem?id=${id}&sale=${sale}`)
        .then(response => {
            selectSubscribeItem.referralBalance = response.data.referralBalance
            selectSubscribeItem.subscribeItem = response.data.subscribeItem
        })
})

const extendSubscribeForBonuses = action(async () => {
    await postAsync("/Payment/ExtendSubscribeForBonuses", {
        positionId: selectSubscribeItem.subscribeItem.id
    }, () => {
        closeLastModal()
        successToast("Подписка успешно продлена")
    })
})

const resetSelectSubscribeItem = action(() => {
    selectSubscribeItem.referralBalance = 0;
    selectSubscribeItem.subscribeItem = null as unknown as IAccessPosition
})

export {subscribeListState, selectSubscribeItem, getSubscribesAsync, getSubscribeItemAsync, resetSelectSubscribeItem, extendSubscribeForBonuses}
