import {useEffect, useRef} from "react";
import {getSubscribeItemAsync, selectSubscribeItem} from "../store";
import {observer} from "mobx-react-lite";
import {Button, FormControl, InputGroup} from "react-bootstrap";
import InputGroupText from "react-bootstrap/InputGroupText";
import {Link} from "react-router-dom";
import {closeLastModal} from "../../../../../common/component/modal/modalStore";

export const BuySubscribe = observer((props: {id: number}) => {
    const saleRef = useRef<HTMLInputElement>(null)

    useEffect(() => {
        getSubscribeItemAsync(props.id, 0)
    }, [props.id]);

    return <div>
        <div>
            <span className="fw-bold-light">Период подписки: </span>{selectSubscribeItem.subscribeItem?.name}
        </div>
        <div>
            <span className="fw-bold-light">Стоимость: </span>{selectSubscribeItem.subscribeItem?.price}руб.
        </div>

        <div className="fw-light mt-2 text-center">
            <Link className="text-secondary" onClick={closeLastModal} to="/referral">Вы можете оплатить до 100% стоимости реферальными бонусами</Link>
        </div>
        <div className="d-flex justify-content-between mt-1">
            <InputGroup className="me-3">
                <InputGroupText id="referal-balance">{selectSubscribeItem.referralBalance}</InputGroupText>
                <FormControl ref={saleRef} aria-describedby="referal-balance"/>
            </InputGroup>

            <Button onClick={() => {

                if(Number(saleRef.current!.value) >= 0) {
                    saleRef.current!.value = selectSubscribeItem.referralBalance >=
                    Number(saleRef.current!.value) ? saleRef.current!.value.toString() : selectSubscribeItem.referralBalance.toString()
                } else
                {
                    saleRef.current!.value = "0"
                }

                getSubscribeItemAsync(props.id, Number(saleRef.current?.value || 0))
            }} variant="dark">Списать</Button>
        </div>
    </div>
})
