import {observer} from "mobx-react-lite";
import {state} from "../store";

export const TicketList = observer(() => {
    return <div className="mt-3">
        <div className="d-none d-lg-flex">
            <div className="w-100">Номер</div>
            <div className="w-100">Тема</div>
            <div className="w-100">Категория</div>
            <div className="w-100">Пользователь</div>
            <div className="w-100">Дата</div>
        </div>

        {
            state.tickets?.map(ticket => {
                return <div key={ticket.id} className="d-block d-sm-flex py-2" style={{cursor: "pointer"}}>
                    <div className="w-100">{ticket.id}</div>
                    <div className="w-100">{ticket.title}</div>
                    <div className="w-100">{ticket.category}</div>
                    <div className="w-100">{ticket.userName}</div>
                    <div className="w-100">{new Date(ticket.createDate).toLocaleDateString()}</div>
                </div>
            })
        }
    </div>
})
