import {Badge, Button} from "react-bootstrap";
import {state} from "../store";
import {observer} from "mobx-react-lite";
import {openModal} from "../../../../common/component/modal/modalStore";
import {ChangePassword} from "./ChangePassword";
import {Index as SubscribeModal} from "../subscibe-modal/Index";

export const Profile = observer(() => {
    return <div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span className="fw-bold-light">Логин:</span> {state.state?.login}
                </div>

                <div>
                    <span className="fw-bold-light">Электронная почта: </span>{state.state?.email}
                </div>
                <div>
                    <span className="fw-bold-light">Дата регистрации: </span>{new Date(state.state?.registerDate).toLocaleDateString()}
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <span className="fw-bold-light">Реферальные бонусы: </span>{state.state?.balance}
                </div>
                <div>
                    <span className="fw-bold-light">Доступ: </span>

                    {new Date(state.state?.endAccessDate) > new Date() &&
                        <Badge bg="success">Активен
                            (до {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }

                    {new Date(state.state?.endAccessDate) <= new Date() &&
                        <Badge bg="danger">Активен
                            (до {new Date(state.state?.endAccessDate).toLocaleDateString()})</Badge>
                    }
                </div>
            </div>
        </div>
        <div className="flex-column d-lg-flex justify-content-between mt-3">
            <div className="d-flex justify-content-around justify-content-lg-start">
                <Button variant="dark" size="sm" onClick={() => openModal(<ChangePassword/>)} className="me-2">Сменить пароль</Button>
                <Button variant="dark" size="sm" onClick={() => openModal(<SubscribeModal/>)} className="me-2">Продлить подписку</Button>
            </div>
        </div>
        <hr/>
    </div>
})
