import {Button, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {closeLastModal} from "../../../../common/component/modal/modalStore";
import {useEffect} from "react";
import {extendSubscribeForBonuses, getSubscribesAsync, resetSelectSubscribeItem, selectSubscribeItem} from "./store";
import {SubscribeList} from "./component/SubscribeList";
import {observer} from "mobx-react-lite";

export const Index = observer(() => {

    useEffect(() => {
        getSubscribesAsync()
    }, []);

    return <>
        <ModalHeader>
            <ModalTitle>Продление подписки</ModalTitle>
        </ModalHeader>
        <ModalBody className="px-3">
            <SubscribeList/>
        </ModalBody>
        <ModalFooter>
            {selectSubscribeItem.subscribeItem !== null && selectSubscribeItem.subscribeItem.price > 0 &&
                <a href={selectSubscribeItem.subscribeItem.link} className="btn btn-dark" target="_blank" rel="noreferrer">
                    Оплатить
                </a>
            }

            {selectSubscribeItem.subscribeItem !== null && selectSubscribeItem.subscribeItem.price === 0 &&
                <Button variant="dark" onClick={extendSubscribeForBonuses}>
                    Продлить подписку
                </Button>
            }

            <Button variant="secondary" onClick={() => {
                resetSelectSubscribeItem()
                closeLastModal()
            }}>
                Закрыть
            </Button>
        </ModalFooter>
    </>
})
