import {RouteObject} from "react-router-dom";

import {Index as LogIndex} from "../../../page/admin/logs/Index"
import {Index as ServersIndex} from "../../../page/admin/servers/Index"
import {Index as FileIndex} from "../../../page/admin/file/Index"
import {Index as TicketManagementList} from "../../../page/admin/ticket-management/ticket-list/Index"
import {Index as TicketManagementView} from "../../../page/admin/ticket-management/ticket-view/Index"
import {PrivateRoute} from "../component/PrivateRoute";
import {getTicketsAsync} from "../../../page/admin/ticket-management/ticket-list/store";
import {getTicketAsync} from "../../../page/admin/ticket-management/ticket-view/store";

export const adminRoute : RouteObject[] = [
    {
        path: "/admin/users",
        element: <></>
    },
    {
        path: "/admin/servers",
        element: <PrivateRoute chiledern={<ServersIndex/>}/>

    },
    {
        path: "/admin/logs",
        element: <PrivateRoute chiledern={<LogIndex/>}/>
    },
    {
        path: "/admin/file",
        element: <PrivateRoute chiledern={<FileIndex/>}/>
    },
    {
        path: "/admin/tickets",
        element: <PrivateRoute chiledern={<TicketManagementList/>}/>,
        loader: async () => await getTicketsAsync()
    },
    {
        path: "/admin/ticket/:ticketId",
        element: <PrivateRoute chiledern={<TicketManagementView/>}/>,
        loader: async ({params}) => await getTicketAsync(params.ticketId!)
    },
]
